// import styles from "./Navigation.module.scss";
import styles from "./SiteHeader.module.scss";
import React from "react";
import Link from "next/link";
import { unstable_renderSubtreeIntoContainer } from "react-dom";

class Navigation extends React.Component {
  state = {
    isOpen: true,
    isSideMenuOpen: false,
    hasScrolled: false,
  };

  componentDidMount() {
    window.addEventListener("scroll", () => {
      if (window.scrollY > 20) {
        this.setState({ hasScrolled: true });
      }
      if (window.scrollY < 20) {
        this.setState({ hasScrolled: false });
      }
    });
  }

  handleClick = () => {
    this.setState({
      isOpen: !this.state.isOpen,
      isSideMenuOpen: !this.state.isSideMenuOpen,
    });
  };
  render() {
    return (
      <header
        className={
          this.state.hasScrolled
            ? "styles.SiteHeader"
            : "styles.SiteHeader__init"
        }
      >
        <div
          className={
            this.state.isOpen
              ? "styles.SiteHeader__menu-icon"
              : "styles.SiteHeader__menu-icon styles.SiteHeader__menu-icon--close-x"
          }
          id="menu-icon"
          onClick={this.handleClick}
        >
          <div className="styles.SiteHeader__menu-icon__middle"></div>
        </div>

        <div
          id="side-menu"
          className={
            this.state.isSideMenuOpen ? "side-nav open-side-menu" : "side-nav"
          }
        >
          <ul className="side-nav-list-items">
            <Link href="/portfolio">
              <a
                className="styles.SiteHeader__listitem"
                onClick={this.handleClick}
              >
                Portfolio
              </a>
            </Link>
            <Link href="/contact">
              <a
                className="styles.SiteHeader__listitem"
                onClick={this.handleClick}
              >
                Contact
              </a>
            </Link>
          </ul>
        </div>
        <div className="content-sizer">
          <nav className="styles.SiteHeader__nav">
            <ul className="styles.SiteHeader__nav-list">
              <div className="styles.SiteHeader__brand-wrapper">
                <Link href="/">
                  <div
                    className={
                      this.state.hasScrolled
                        ? "styles.SiteHeader__brand-name"
                        : "styles.SiteHeader__brand-name-init"
                    }
                  >
                    <div className="styles.SiteHeader__brand-wrapper__line"></div>
                    <div className="styles.SiteHeader__brand-name__the">
                      The
                    </div>
                    <div className="styles.SiteHeader__brand-name__fine-web">
                      Fine Web
                    </div>
                  </div>
                </Link>
              </div>
              <div className="styles.SiteHeader__list-item-wrapper">
                <li className="styles.SiteHeader__nav-list-item">
                  <Link href="/portfolio">
                    <a className="styles.SiteHeader__listitem">Portfolio</a>
                  </Link>
                </li>
                <li>
                  <Link href="/contact">
                    <a className="styles.SiteHeader__listitem">Contact</a>
                  </Link>
                </li>
              </div>
            </ul>
          </nav>
        </div>
      </header>
    );
  }
}

export default Navigation;
